.vetskitchen-dogclub-banner {
  position: relative;
  padding-top: 60px;
  padding-bottom: 200px;
  font-family: "Signika", sans-serif;
  text-align: center;
  background: #eee url("../../assets/images/dogclub-banner.webp") no-repeat;
  background-size: cover;
  border-radius: 15px;
}
.vetskitchen-dogclub-banner .acf-innerblocks-container {
  margin: 0 auto;
  max-width: 460px;
}
.vetskitchen-dogclub-banner h2 {
  font-size: 40px;
  font-weight: 600;
  line-height: 1.1;
  color: #2689de;
}
.vetskitchen-dogclub-banner .wp-block-button {
  margin-top: 50px;
}
@media screen and (min-width: 1025px) {
  .vetskitchen-dogclub-banner h2 {
    margin-bottom: 23px;
    font-size: 62px;
  }
}